let that;
let list = {
  data() {
    return {
      formData: {
        username: '',
        password: '',
        checkPwd: '',
        dtype: '',
        a_id: '',
        phone: '',
        deadline: ''
      },
      formData2: {
        username: '',
        password: '',
        checkPwd: '',
        dtype: '',
        a_id: '',
        phone: '',
        deadline: ''
      },
      utype: [{
        label: '管理员',
        value: 1
      }, {
        label: '区域代理',
        value: 2
      }],
      loading: false
    }
  },
  mounted() {
    that = this
  },
  methods: {
    onSubmit() {
      this.loading = true

      function getFormatDate() {
        var date = new Date();
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
           month = "0" + month;
       }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentDate = date.getFullYear() + "-" + month + "-" + strDate
             + " " + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();
        return currentDate;
      }

      let formData = {
        username: this.formData.username,
        password: this.yzy.encrypt(this.formData.password),
        checkPwd: this.yzy.encrypt(this.formData.checkPwd),
        a_id: this.formData.a_id,
        dtype: this.formData.dtype,
        phone: this.formData.phone,
        deadline: getFormatDate()
      }
      formData.password = this.yzy.encrypt(formData.password)
      formData.checkPwd = this.yzy.encrypt(formData.checkPwd)
      this.yzy.post('user/register', formData, function (res) {
        that.loading = false
        if (res.code == 1) {
          that.$message.success(res.msg)
          that.formData = that.formData2
        } else {
          that.$message.error(res.msg)
        }
      })
    }
  }
}
module.exports = list



